@media only screen and (min-width: 768px) {
  /* Apply magnification only on desktop viewports */
  .magnify-cursor:hover {
    transform: scale(1.25); /* Magnify the cursor */
  }
}

/* Default styles */

.App {
  text-align: center;
  margin: 0;
  background-image: url("./components/sampleBG.jpg");
  background-size: contain; 
  background-repeat: repeat; 
  background-attachment: fixed;
  height: 100vh;
  
}

button {
  cursor: pointer;
}

.game-box {
  width: 50%;
  text-align: left;
  margin: 2rem auto; /* Center the game box */
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 2rem;
  border-radius: 15px;
}

.header-container {
  color: white;
  background-color: rgb(0, 0, 0);
}

.header-image {
  width: 10rem;
  height: 5rem;
  margin-right: 10px;
}

.title-text {
  font-size: 2rem;
  padding: 1rem;
  font-weight: bolder;
}

.prompt-box-label {
  margin-top: 3rem;
}

.prompt-container {
  background-color: rgba(206, 206, 206, 0.4);
  padding: 1rem;
  border-radius: 10px;
  z-index: -1;
}

.next-prev-btn-cluster {
  text-align: center;
  margin-top: 1.5rem;
}

.prev-btn {
  margin-right: 31.5rem;
}

.pnb-btn {
  width: 7rem;
  height: 2.5rem;
  border: none;
  background-color: rgb(126, 0, 0);
  color: white;
  font-size: 1rem;
  font-weight: bolder;
  border-radius: 5px;
}

.pnb-btn:hover {
  background-color: rgb(163, 0, 0);
}

.ans-btn {
  margin-top: 1rem;
  border: none;
  background-color: red;
  height: auto;
  font-size: 1rem;
  color: white;
  font-weight: bold;
  width: auto;
  border-radius: 5px;
  margin-right: 1rem;
  padding: 10px;
}

.ans-btn:hover {
  background-color: orange;
}

.hint-btn {
  width: 10rem;
  margin-left: 2rem;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black */
  z-index: 1000; /* Ensure the overlay appears above other content */
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 20px;
  color: #888;
}

.close-btn:hover {
  color: #333;
}

/* Media queries for responsiveness */

@media screen and (max-width: 768px) {
  .game-box {
    width: 80%; /* Adjust width for smaller screens */
    margin: 2rem auto; /* Center the game box */
  }

  .prev-btn {
    margin-right: 10rem; /* Adjust margin for smaller screens */
  }
}

@media screen and (max-width: 480px) {
  .game-box {
    width: 90%; /* Adjust width for smaller screens */
    margin: 2rem auto; /* Center the game box */
  }

  .prev-btn {
    margin-right: 5rem; /* Adjust margin for smaller screens */
  }
}
